@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  background-color: #151515;
}
.flex-all-center{
  display: flex;
  align-items: center;
  justify-content: center;
}
.flex-all-center-col{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.flex-space-between{
  display: flex;
  justify-content: space-between;
}
.flex-space-evenly{
  display: flex;
  justify-content: space-evenly;
}
.hero-section{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.align-with-hero{
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
.hero-section-hello{
  text-align: left;
  font-weight: 400;
  font-size: 40px;
  margin-bottom: 0px;
  display: flex;
  color:white;
}
.floating-hand{
  animation: float 2s ease-in-out infinite;
  transform: translateY(0px);  
  margin-left: 10px;
}
@keyframes float {
	0% {
		transform: translateY(0px);
	}
  50%{
    transform: translateY(-10px);
  }
	100% {
		transform: translateY(0px);
	}
}
.center-img{
  text-align: center;
}
.introduction{
  margin-top: 60px;
  text-align: left;
  font-weight: 300;
  font-size: 18px;
  letter-spacing: -0.4px;
  display: flex;
  color:white;
  margin-bottom: 40px;
}
.main-button{
  background-color: #219B05;
  border:1px solid #219B05;
  text-decoration: none;
  color: white;
  padding:15px 20px;
  font-size: 16px;
  font-weight: 300;
  width: 190px;
  border-radius: 5px;
  text-align: center;
  transition: 0.3s;
}
.main-button:hover{
  background-color: #177203;
  border: 1px solid #177203;
  color:white !important;
}
.main-outline-button{
  border-radius: 5px;
  border:1px solid #219B05;
  text-decoration: none;
  color: white;
  padding:15px 20px;
  font-weight: 300;
  font-size: 16px;
  width: 190px;
  text-align: center;
  transition: 0.3s;
}
.main-outline-button:hover{
  background-color: #1b1b1b;
  color:white !important;
}
.section-heading{
  font-size: 50px;
  color:white;
  margin-bottom: 40px;
  text-align: center;
  letter-spacing: -0.4px;
}
.section-start{
  padding:120px 0px;
}
.section-end{
  padding-bottom: 120px;
}
.green-span{
  color:#219B05;
}
.experience-main{
  background-color: #232323;
  width: 100%;
  border-radius: 5px;
  padding:40px 20px;
  position: relative;
  margin-bottom: 40px;
}
.experience-main .my-company-name{
  font-size: 25px;
  color: white;
  text-align: center;
  margin-bottom: 0px;
  margin-top: 30px;
  font-weight: 600;
}
.experience-main .duration{
  font-size: 16px;
  color: white;
  text-align: center;
  margin-bottom: 0px;
  margin-top: 10px;
  font-weight: 300;
  opacity: 0.6;
}
.experience-main .my-company-description{
  font-size: 16px;
  color: white;
  text-align: center;
  margin-bottom: 40px;
  margin-top: 30px;
  width: 70%;
  font-weight: 300;
}
.pulse-main{
  position: absolute;
  top:20px;
  left:20px;
}
.pulse-main .circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  box-shadow: 0px 0px 1px 1px #219B05;
}
.pulse-main .green{
  background-color: #219B05;
}
.pulse-main .pulse {
  animation: pulse-animation 1s infinite;
}

@keyframes pulse-animation {
  0% {
    box-shadow: 0 0 0 0px rgba(33, 155, 5, 0.2);
  }
  100% {
    box-shadow: 0 0 0 10px rgba(33, 155, 5, 0.2);
  }
}
.small-heading{
  font-size: 30px;
  color:white;
  margin-bottom: 60px;
  margin-top: 30px;
  text-align: center;
  letter-spacing: -0.4px;
  opacity: 0.7;
}
.experience-head{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.previous-experience{
  position: absolute;
  z-index: 1;
}
.experience-box{
  z-index: 2;
  position: relative;
  background-color: #232323;
  width: 100%;
  border-radius: 5px;
  margin-top: 60px;
  padding:30px;
}
.experience-box .company-name{
  font-size: 24px;
  color:white;
  margin-bottom: 30px;
  font-weight: 600;
  text-align: center;
}
.experience-box .work-duration{
  font-size: 16px;
  color:white;
  margin-bottom: 0px;
  font-weight: 400;
  text-align: left;
}
.experience-box .work-role{
  font-size: 16px;
  color:white;
  margin-bottom: 0px;
  font-weight: 400;
  text-align: right;
}

.awards-section{
  background-color: #232323;
}
.awards{
  background-color: #151515;
  border-radius: 5px;
  padding:30px;
  width: 100%;
  position: relative;
  display: flex;
  margin-top: 60px;
}
.awards .medal-picture{
  width: 120px;
  height: 120px;
}
.winner{
  font-size: 16px;
  color:#36d014;
  margin-top: 30px;
  margin-bottom: 0px;
  font-weight: 300;
  text-align: center;
}
.one{
  position:absolute;
  z-index: 2;
  left:-40px;
}
.award-name{
  font-size: 16px;
  color:white;
  margin-top: 10px;
  margin-bottom: 0px;
  font-weight: 300;
  text-align: center;
}
.awards-2{
  margin-top: 60px;
}
.projects{
  background-color: #232323;
  padding-top: 60px;
  margin-bottom: 50px;
  border-radius: 10px;
}
.projects .project-name{
  font-size: 30px;
  color:white;
  margin-top: 0px;
  margin-bottom: 30px;
  font-weight: 600;
  text-align: center;
}
.projects .project-description{
  font-size: 16px;
  color:white;
  margin-top: 0px;
  margin-bottom: 30px;
  font-weight: 300;
  text-align: center;
  width: 50%;
}
.projects .project-img{
  margin-top: 40px;
}